import { ActivatedRoute } from "@angular/router";
import { HttpClient } from '@angular/common/http';
import { ResultsService } from "../results.service";
var ResultsComponent = /** @class */ (function () {
    function ResultsComponent(route, http, resultsService) {
        this.route = route;
        this.http = http;
        this.resultsService = resultsService;
        this.id = '';
        this.stats = {
            filesCount: 0,
            errors: [],
        };
        this.showEmptyResults = false;
    }
    ResultsComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.route.params.subscribe(function (params) {
            _this.id = params.id;
            // Todo: handle if not found.
            _this.resultsService.getResults(params.id).subscribe(function (response) {
                _this.results = Object.keys(response.results);
                _this.originalResults = _this.results;
                _this.stats = {
                    'filesCount': Object.keys(_this.results).length,
                    'errors': [
                        { 'text': '   -  ', 'times': 5 },
                        { 'text': '   -  ', 'times': 10 },
                        { 'text': ' 0   2 ', 'times': 9 },
                        { 'text': 'DD/MM/YYYY    ', 'times': 30 },
                    ],
                };
            });
        });
    };
    ResultsComponent.prototype.filterFiles = function (event) {
        var text = event.target.value;
        this.results = this.originalResults;
        this.showEmptyResults = false;
        if (text == '') {
            this.results = this.originalResults;
            return;
        }
        this.results = this.results.filter(function (item) { return item.indexOf(text) != -1; });
        if (this.results.length == 0) {
            this.showEmptyResults = true;
        }
    };
    return ResultsComponent;
}());
export { ResultsComponent };



