import { ActivatedRoute } from "@angular/router";
import { MetadataService } from "../metadata.service";
import { HttpClient } from "@angular/common/http";
import { environment } from "../../environments/environment";
import { ResultsService } from "../results.service";
var FileComponent = /** @class */ (function () {
    function FileComponent(route, http, metadata, resultsService) {
        this.route = route;
        this.http = http;
        this.metadata = metadata;
        this.resultsService = resultsService;
        this.id = '';
        this.results = [];
        this.processing = false;
        this.activeClass = '';
        this.tabs_maximum = {};
        this.meta = {};
        this.filename = '';
    }
    FileComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.route.params.subscribe(function (params) {
            _this.id = params.id;
            _this.filename = params.file;
            _this.metadata.getMetadata().subscribe(function (data) {
                _this.meta = data;
                _this.resultsService.getResults(params.id).subscribe(function (response) {
                    var results = response.results;
                    if (results != null) {
                        _this.processing = true;
                        _this.results = _this.processData(results, _this.filename);
                        _this.processing = false;
                    }
                    else {
                        _this.processing = true;
                        _this.http.get(environment.backend + 'process_files/' + params.id).subscribe(function (data) {
                            // Process the results.
                            _this.processData(data.results, _this.filename);
                            _this.processing = false;
                        }, function (err) {
                            console.log(err);
                        });
                    }
                });
            });
        });
    };
    /**
     * Process the results into HTML.
     *
     * @param results
     *   The response, could be from the local storage or from an HTTP request in case it does not exists there.
     * @param file
     *   The name of the file.
     */
    FileComponent.prototype.processData = function (results, file) {
        this.activeClass = 'cash';
        var self = this;
        self.tabs_maximum[file] = {};
        Object.keys(results[file]).forEach(function (tab) {
            Object.keys(results[file][tab]).forEach(function (column) {
                self.tabs_maximum[tab] = Object.keys(results[file][tab][column]).map(function (key) {
                    return parseInt(key);
                });
            });
        });
        return results[file];
    };
    FileComponent.prototype.setTab = function (tab) {
        this.activeClass = tab;
    };
    return FileComponent;
}());
export { FileComponent };



