import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
var UploadComponent = /** @class */ (function () {
    function UploadComponent(http, router) {
        this.http = http;
        this.router = router;
        this.config = {};
        this.data = {};
        this.canVerify = false;
        this.processing = false;
    }
    UploadComponent.prototype.ngOnInit = function () {
        this.data['room'] = Date.now();
        this.data['files'] = [];
    };
    UploadComponent.prototype.onUploadError = function ($event) {
    };
    UploadComponent.prototype.onUploadSuccess = function ($event) {
        this.canVerify = true;
        this.data['files'].push($event[1].file);
    };
    UploadComponent.prototype.submit = function () {
        this.processing = true;
        this.canVerify = false;
        var body = new URLSearchParams();
        body.set('files', this.data['files']);
        body.set('room', this.data['room']);
        var options = {
            headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
        };
        var router = this.router;
        // Send it to the backend and start to wait for the pusher events.
        this.http.post(environment.backend + 'process_files', body.toString(), options).subscribe(function (data) {
            router.navigate(["/results/" + data.data.id]);
        }, function (err) {
            console.log(err);
        });
    };
    return UploadComponent;
}());
export { UploadComponent };



